@mixin H1_Desktop() {
	
    font-family: $fontHeadings;
    font-size: 48px;
    font-weight: $regular;
    line-height: 1.23;
}

@mixin H4_Desktop() {
	
    font-family: $fontHeadings;
    font-size: 24px;
    font-weight: $regular;
    line-height: 1.5;
}

@mixin Text16_Desktop() {
	
    font-size: 16px;
    font-weight: $regular;
    line-height: 1.38;
}

@mixin Text18_Desktop() {
	
    font-size: 18px;
    font-weight: $medium;
    line-height: 1.4;
}
 

@mixin _1400 {
    @media (max-width: 1440px){
        @content;
    }
}

@mixin _1200 {
    @media (max-width: 1240px){
        @content;
    }
}

@mixin _1024 {
    @media (max-width: 1024px){
        @content;
    }
}

@mixin _892 {
    @media (max-width: 892px){
        @content;
    }
}

@mixin _768 {
    @media (max-width: 767.98px){
        @content;
    }
}

@mixin _575 {
    @media (max-width: 575.98px){
        @content;
    }
}

@mixin _485 {
    @media (max-width: 485.98px){
        @content;
    }
}

@mixin _395 {
    @media (max-width: 395.98px){
        @content;
    }
}

